// Function to analyze student profile
async function analyzeProfile() {
    // Get input values
    const exam_type = document.getElementById('exam_type').value;
    const rank_or_percentile = document.getElementById('rank_or_percentile').value;
    const category = document.getElementById('category').value;
    const gender = document.getElementById('gender').value;
    const preferred_course = document.getElementById('preferred_course').value;

    // Log input values for debugging
    console.log({
        exam_type: exam_type,
        rank_or_percentile: parseInt(rank_or_percentile, 10),
        category: category,
        gender: gender,
        preferred_course: preferred_course
    });

    try {
        const response = await fetch('http://127.0.0.1:5000/predict', {
            method: 'POST',
            headers: {
                'Content-Type': 'application/json',
            },
            body: JSON.stringify({
                exam_type: exam_type,
                rank_or_percentile: parseInt(rank_or_percentile, 10),
                category: category,
                gender: gender,
                preferred_course: preferred_course
            })
        });

        if (!response.ok) {
            throw new Error("Network response was not ok");
        }

        const result = await response.json();

        // Check the admission status and prepare the output
        if (result.admission === "Accepted") {
            let collegesInfo = "";

            // Assuming the result contains college details
            result.colleges.forEach(college => {
                collegesInfo += `
                    <strong>College:</strong> ${college['Institute']}<br>
                    <strong>Branch:</strong> ${college['Branch']}<br>
                    <strong>Cutoff:</strong> ${college['Cutoff']}<br>
                `;
            });

            document.getElementById('result').innerHTML = `
                <strong>Admission Status:</strong> Accepted!<br>
                Here are the possible colleges for your chosen branch:<br><br>
                ${collegesInfo}
            `;
        } else {
            document.getElementById('result').innerText = "Admission Status: Rejected. Unfortunately, you didn't qualify for admission.";
        }

    } catch (error) {
        console.error('Error:', error);
        document.getElementById('result').innerText = 'Error: Unable to analyze profile.';
    }
}

// Wait for the DOM to load before adding event listeners
document.addEventListener('DOMContentLoaded', function() {
    document.querySelector('.btn').addEventListener('click', analyzeProfile);
});
